import React, { useCallback, useEffect, useMemo, useState } from 'react'
import Methods from 'components/Methods'

import { Approve } from 'components/CommonComponent'
import useToast from 'hooks/useToast'
import DataWrapper from 'components/DataWrapper'
import { useComputedIndexAction, usePositionInfo, usePositionAction } from 'hooks/news/usePoistion'
import { AvatarIcon, GuaranteeIcon } from 'components/uikit'
import { TransactionSubmittedInfo, TransactionSuccessInfo } from 'components/TransactionInfo'
import { DataIndexName, ToastTitle } from 'config/constants/contants'
import Logo from 'components/Logo'
import { ethers } from 'ethers'

function AddGuaranteeFromAvatar(props) {
  const { approveStatus, onApprove, walletTokenBalance } = props
  const [transferVal, setTransferVal] = useState<number>(0)
  const [isDisabled, setDisabled] = useState(false)
  const { toastSuccess, toastError, toastInfo } = useToast()
  const { collateralIndex, currentPosition, currentPositionTokenInfo } = usePositionInfo()
  const { onUpdatePrepaidInterest, newDisplayPrepaidInterestCoverage } = useComputedIndexAction()
  const { handleUpdatePosition, onAddPrepaidInterest } = usePositionAction()
  const leftBalance = useMemo(() => {
    console.log('currentPositionTokenInfo.allowedTokens?.balances[currentPosition.mintTokenInfo.settlementCurrency.symbol]', )
    return ethers.utils.formatUnits(
      currentPositionTokenInfo.allowedTokens?.balances[currentPosition.mintTokenInfo.settlementCurrency.symbol] ?? 0,
      currentPosition.mintTokenInfo.settlementCurrency.decimals,
    ) as any
  }, [currentPosition.mintTokenInfo.settlementCurrency, currentPositionTokenInfo.allowedTokens?.balances])

  const changeTransferVal = useCallback(
    (e, type) => {
      const numberVal = type === 0 ? Number(e.target.value) : leftBalance
      setTransferVal(type === 0 ? e.target.value : numberVal)
      onUpdatePrepaidInterest(numberVal)
    },
    [leftBalance, onUpdatePrepaidInterest],
  )
  const onSubmit = useCallback(async () => {
    try {
      console.log('transferVal', transferVal)
      if (!transferVal || (transferVal > leftBalance && transferVal <= 0)) {
        toastError(ToastTitle.failed, 'Please enter a valid amount')
        return
      }
      setDisabled(true)
      const txInfo = await onAddPrepaidInterest(transferVal, true)
      toastInfo(
        ToastTitle.submited,
        <TransactionSubmittedInfo content="Waiting for confirmation" txHash={txInfo.hash} />,
      )
      const result = await txInfo.wait()
      if (result) {
        toastSuccess(
          ToastTitle.success,
          <TransactionSuccessInfo content="Settlement added successfully" txHash={txInfo.hash} />,
        )
        setDisabled(false)
        setTransferVal(0)
        onUpdatePrepaidInterest(0)
        await handleUpdatePosition()
      }
    } catch (error) {
      // console.log('transferVal > leftBalance || transferVal <= 0', transferVal > leftBalance || transferVal <= 0, transferVal, leftBalance)
      // setDisabled(transferVal > leftBalance || transferVal <= 0)
    }
  }, [
    transferVal,
    leftBalance,
    onAddPrepaidInterest,
    toastInfo,
    toastSuccess,
    handleUpdatePosition,
    onUpdatePrepaidInterest,
    toastError,
  ])
  useEffect(() => {
    if (transferVal > leftBalance || transferVal <= 0) {
      setDisabled(true)
    } else {
      setDisabled(false)
    }
  }, [transferVal, leftBalance])
  return (
    <>
      <Methods
        SelectIcon={AvatarIcon}
        options={[
          {
            token: currentPosition.mintTokenInfo.settlementCurrency.symbol,
            balance: walletTokenBalance
              ? walletTokenBalance[currentPosition.mintTokenInfo.settlementCurrency.symbol]
              : 0,
            symbol: currentPosition.mintTokenInfo.settlementCurrency.symbol,
            icon: () => (
              <Logo
                imgprops={{
                  style: { marginRight: 10 },
                }}
                token={currentPosition.mintTokenInfo.settlementCurrency.logo}
                symbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
              />
            ),
            address: currentPosition.mintTokenInfo.settlementCurrency.address,
            rightOptionList: [
              {
                token: currentPosition.mintTokenInfo.settlementCurrency.symbol,
                symbol: currentPosition.mintTokenInfo.settlementCurrency.symbol,
                icon: () => (
                  <Logo
                    imgprops={{
                      style: { marginRight: 10 },
                    }}
                    token={currentPosition.mintTokenInfo.settlementCurrency.logo}
                    symbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
                  />
                ),
                address: currentPosition.mintTokenInfo.settlementCurrency.address,
                balance: collateralIndex.prepaidInterest,
              },
            ],
          },
        ]}
        rightBalance={collateralIndex.prepaidInterest}
        leftBalance={leftBalance}
        rightSymbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
        selectedSymbol={currentPosition.mintTokenInfo.settlementCurrency.symbol}
        RightIcon={GuaranteeIcon}
        leftInputValue={transferVal}
        rightInputValue={transferVal}
        selectInput={(e) => {
          changeTransferVal(e, 0)
        }}
        onMax={() => {
          changeTransferVal({}, 1)
        }}
      />
      <DataWrapper
        data={[
          // The Collateral Ratio has not changed
          [DataIndexName.CollateralRatio, collateralIndex.displayRatio, collateralIndex.displayRatio],

          [DataIndexName.PrepaidInterestCoverage, collateralIndex.displayCoverage, newDisplayPrepaidInterestCoverage],
        ]}
      />
      <Approve
        style={{ boxShadow: 'none' }}
        disabled={!approveStatus || isDisabled || !(transferVal > 0)}
        onClick={approveStatus ? onSubmit : onApprove}
      >
        {approveStatus ? 'Submit' : 'Approve'}
      </Approve>
    </>
  )
}

export default AddGuaranteeFromAvatar
